$primary: #444df2;
$accent: #29f294;
$white: white;
$black: black;
$background: #fafafa;

$transition: all 0.3s linear;

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background: $background;
  color: $black;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

button {
  outline: none;
  border: none;
}

// Home
main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sidebar-toggle {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 2rem;
  left: 3rem;
  font-size: 2rem;
  transition: $transition;
  background: none;
  color: $primary;
  cursor: pointer;
  animation: bounce 2s ease-in-out infinite;

  &:before {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    position: absolute;
    content: "";
    height: 3.4rem;
    width: 3.4rem;
    background: lighten($primary, 36%);
    z-index: -1;
    opacity: 0;
    transition: $transition;
  }

  &:hover {
    &:before {
      opacity: 1;
    }
  }
}

@keyframes bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.btn {
  text-transform: uppercase;
  background: transparent;
  color: $primary;
  border: 2px solid $primary;
  padding: 0.8rem 2rem;
  transition: $transition;
  cursor: pointer;
  margin: 0.5rem;
  letter-spacing: 0.05rem;
  border-radius: 0.4rem;
  font-family: inherit;
  font-weight: 600;

  &:hover {
    background: $primary;
    border: 2px solid transparent;
    color: $white;
    font-weight: 500;
  }
}

// Sidebar
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $primary;
  display: grid;
  grid-template-rows: auto 1fr auto;
  row-gap: 1rem;
  transition: transform .3s ease-in-out;
  transform: translate(-100%);
}

.show-sidebar {
  transform: translate(0);
}

@media screen and (min-width: 676px) {
  .sidebar {
    width: 24rem;
  }
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  background: #3a41c9;

  h2 {
    font-family: 'Proxima Nova';
    font-size: 2rem;
    color: $white;
  }
}

.close-btn {
  font-size: 1.75rem;
  background: transparent;
  border-color: transparent;
  transition: $transition;
  cursor: pointer;
  color: $white;
  margin-top: 0.2rem;

  &:hover {
    transform: scale(1.1);
    color: $accent;
  }
}

.links {
  a {
    display: flex;
    align-items: center;
    font-size: 1rem;
    text-transform: capitalize;
    padding: 1.5rem 1.5rem;
    transition: $transition;
    color: $white;

    &:hover {
      background: lighten($primary, 2%);
      padding-left: 2rem;
      border-left: 0.5rem solid $accent;
    }

    svg {
      font-size: 1.5rem;
      margin-right: 1rem;
      transition: $transition;
    }
  }
}

.social-icons {
  justify-self: center;
  display: flex;
  padding-bottom: 2rem;

  a {
    font-size: 1.4rem;
    margin: 0 1rem;
    transition: $transition;
    color: $white;

    &:hover {
      color: $accent;
    }
  }
}

// Modal
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
  transition: $transition;
  visibility: hidden;
  z-index: -1;
}

.show-modal {
  visibility: visible;
  z-index: 10;
}

.modal-container {
  background: $white;
  border-radius: 0.5rem;
  width: 90vw;
  height: 30vh;
  max-width: 620px;
  text-align: center;
  display: grid;
  place-items: center;
  position: relative;

  h3 {
    text-transform: capitalize;
    font-size: 1.5rem;
    letter-spacing: 0.03rem;
  }
}

.close-modal-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 2rem;
  background: transparent;
  border-color: transparent;
  cursor: pointer;
}
